.intro {
  position: relative;
  overflow: hidden;
  box-sizing: border-box;
  min-height: 775px;
  padding: 0;
  padding-bottom: 20px;
  background-color: $color-black-pearl;

  @include vp-1023 {
    min-height: 552px;
  }

  @include vp-767 {
    min-height: 500px;
  }

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    display: block;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, rgba($color-black-pearl-darker, 0.7) 71.72%, rgba($color-black-pearl-darker, 0) 100%);
  }

  img {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: 50% 100%;

    @include vp-1023 {
      top: -15.5%;
      height: 115.5%;
      object-position: 20% 90%;
    }

    @include vp-767 {
      top: -11.5%;
      height: 111.5%;
      object-position: 25.5% 0;
    }
  }
}

.intro__container {
  position: relative;
  z-index: 2;
  box-sizing: border-box;
  padding-left: 165px;
  padding-top: 195px;
  color: $color-alice-blue-text;

  @include vp-1023 {
    padding-top: 89px;
  }

  @include vp-767 {
    padding-top: 88px;
  }

  h2 {
    margin: 0;
    margin-bottom: 38px;
    padding: 0;
    font-family: $ff-Roboto-Condensed;
    font-style: normal;
    font-weight: 700;
    font-size: 80px;
    line-height: 94px;
    letter-spacing: 0.08em;
    text-transform: uppercase;

    @include vp-1023 {
      margin-bottom: 3px;
      font-size: 64px;
      line-height: 75px;
    }

    @include vp-767 {
      margin-bottom: 10px;
      font-size: 36px;
      line-height: 42px;
    }
  }

  p {
    max-width: 685px;
    margin: 0;
    margin-bottom: 45px;
    padding: 0;
    font-size: 22px;
    line-height: 155%;

    @include vp-1023 {
      margin-bottom: 56px;
    }

    @include vp-767 {
      font-size: 14px;
      margin-bottom: 45px;
    }
  }
}

.intro__link {
  width: 410px;
  margin-bottom: 10px;
  padding: 15px 25px 19px 25px;
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0.06em;
  background-color: $color-deep-sky-blue;
  color: inherit;

  @include hover-focus {
    background-color: $color-alice-blue-bg;
    color: $color-black-pearl;
  }

  @include vp-1023 {
    margin-left: 46px;
    padding: 16px 25px 18px 22px;
    letter-spacing: 0.06em;
  }

  @include vp-767 {
    width: 100%;
    margin-left: 0;
    padding: 12px;
    font-size: 14px;
    line-height: 16px;
  }
}

.intro__link-mobile {
  display: none;

  @include vp-767 {
    display: inline;
  }
}

.intro__link-desktop {
  @include vp-767 {
    display: none;
  }
}
